import Dashboard from './components/dashboard/Dashboard';

function App() {
  return (
    <BrowserRouter>
      <div className="App">
        <Navbar />
        <Switch>
          <Router path="/" component={Dashboard}></Router>
        </Switch>
      </div>
    </BrowserRouter>
  );
}

export default App;
